import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlin.Any
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable
import org.optimum_tech.quranify.components.layouts.PageLayout as _org_optimum_tech_quranify_components_layouts_PageLayout
import org.optimum_tech.quranify.pages.HomeNew as _org_optimum_tech_quranify_pages_HomeNew
import org.optimum_tech.quranify.pages.about.About as _org_optimum_tech_quranify_pages_about_About
import org.optimum_tech.quranify.pages.apps.Apps as _org_optimum_tech_quranify_pages_apps_Apps
import org.optimum_tech.quranify.pages.contacts.Contact as _org_optimum_tech_quranify_pages_contacts_Contact
import org.optimum_tech.quranify.pages.delete_account.DeleteAccount as _org_optimum_tech_quranify_pages_delete_account_DeleteAccount
import org.optimum_tech.quranify.pages.policy.PrivacyPolicy as _org_optimum_tech_quranify_pages_policy_PrivacyPolicy

private val LayoutScopeLocal: ProvidableCompositionLocal<Any?> = compositionLocalOf<Any?> { null }

@Composable
private fun <T : Any> currentLayoutScope(): T {
    @Suppress("UNCHECKED_CAST")
    return LayoutScopeLocal.current as? T
        ?: error("Unexpected between Page/Layout scope and the parent Layout which it is providing it. Please report this issue to the Kobweb developers.")
}

@Composable
private fun <T : Any> provideLayoutScope(layoutScope: T, content: @Composable () -> Unit) {
    androidx.compose.runtime.CompositionLocalProvider(LayoutScopeLocal provides layoutScope, content = content)
}

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Quranify - quranic vocabulary"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.registerLayout(
            "org.optimum_tech.quranify.components.layouts.PageLayout",
        ) { pageCtx, pageMethod -> 
            _org_optimum_tech_quranify_components_layouts_PageLayout(pageCtx) {
                provideLayoutScope(this) {
                    pageMethod(pageCtx)
                }
            }
        }
        ctx.router.register(
            "/",
            layoutId = "org.optimum_tech.quranify.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                org.optimum_tech.quranify.pages.initHome(ctx)
            },
        ) { pageCtx -> 
            _org_optimum_tech_quranify_pages_HomeNew()
        }
        ctx.router.register(
            "/about",
            layoutId = "org.optimum_tech.quranify.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                org.optimum_tech.quranify.pages.about.initAboutPage(ctx)
            },
        ) { pageCtx -> 
            _org_optimum_tech_quranify_pages_about_About()
        }
        ctx.router.register(
            "/apps",
            layoutId = "org.optimum_tech.quranify.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                org.optimum_tech.quranify.pages.apps.initAppsPage(ctx)
            },
        ) { pageCtx -> 
            _org_optimum_tech_quranify_pages_apps_Apps()
        }
        ctx.router.register(
            "/contact",
            layoutId = "org.optimum_tech.quranify.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                org.optimum_tech.quranify.pages.contacts.initContactPage(ctx)
            },
        ) { pageCtx -> 
            _org_optimum_tech_quranify_pages_contacts_Contact()
        }
        ctx.router.register(
            "/delete-account",
            layoutId = "org.optimum_tech.quranify.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                org.optimum_tech.quranify.pages.delete_account.initDeletePage(ctx)
            },
        ) { pageCtx -> 
            _org_optimum_tech_quranify_pages_delete_account_DeleteAccount()
        }
        ctx.router.register(
            "/privacy-policy",
            layoutId = "org.optimum_tech.quranify.components.layouts.PageLayout",
            initRouteMethod = { ctx ->
                org.optimum_tech.quranify.pages.policy.initPrivacyPolicyPage(ctx)
            },
        ) { pageCtx -> 
            _org_optimum_tech_quranify_pages_policy_PrivacyPolicy()
        }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("headline-text", org.optimum_tech.quranify.HeadlineTextStyle)
        ctx.theme.registerStyle("subheadline-text", org.optimum_tech.quranify.SubheadlineTextStyle)
        ctx.theme.registerStyle("footer", org.optimum_tech.quranify.components.sections.FooterStyle)
        ctx.theme.registerStyle("nav-bar", org.optimum_tech.quranify.components.sections.NavBarStyle)
        ctx.theme.registerStyle("nav-container", org.optimum_tech.quranify.components.sections.NavContainerStyle)
        ctx.theme.registerStyle("logo", org.optimum_tech.quranify.components.sections.LogoStyle)
        ctx.theme.registerStyle("cta-button", org.optimum_tech.quranify.components.sections.CTAButtonStyle)
        ctx.theme.registerStyle("nav-link", org.optimum_tech.quranify.components.sections.NavLinkStyle)
        ctx.theme.registerStyle("platform-card", org.optimum_tech.quranify.components.widgets.PlatformCardStyle)
        ctx.theme.registerStyle("close-button", org.optimum_tech.quranify.components.widgets.CloseButtonStyle)
        ctx.theme.registerStyle("step-card", org.optimum_tech.quranify.components.widgets.StepCardStyle)
        ctx.theme.registerStyle("download-button", org.optimum_tech.quranify.components.widgets.DownloadButtonStyle)
        ctx.theme.registerStyle("play-store-image", org.optimum_tech.quranify.components.widgets.PlayStoreImageStyle)
        ctx.theme.registerStyle("modal", org.optimum_tech.quranify.components.widgets.ModalStyle)
        ctx.theme.registerStyle("modal-content-column", org.optimum_tech.quranify.components.widgets.ModalContentColumnStyle)
        ctx.theme.registerStyle("modal-title", org.optimum_tech.quranify.components.widgets.ModalTitleStyle)
        ctx.theme.registerStyle("modal-button-row", org.optimum_tech.quranify.components.widgets.ModalButtonRowStyle)
        ctx.theme.registerStyle("full-width-children", org.optimum_tech.quranify.components.widgets.FullWidthChildrenStyle)
        ctx.theme.registerStyle("feature-item", org.optimum_tech.quranify.pages.featureItemStyle)
        ctx.theme.registerStyle("main-layout", org.optimum_tech.quranify.pages.mainLayoutStyle)
        ctx.theme.registerStyle("hero-container", org.optimum_tech.quranify.pages.about.heroContainerStyle)
        ctx.theme.registerStyle("section-card", org.optimum_tech.quranify.pages.about.sectionCardStyle)
        ctx.theme.registerStyle("floating-element", org.optimum_tech.quranify.pages.about.floatingElementStyle)
        ctx.theme.registerStyle("stat-card", org.optimum_tech.quranify.pages.about.statCardStyle)
        ctx.theme.registerStyle("contact-button", org.optimum_tech.quranify.pages.about.contactButtonStyle)
        ctx.theme.registerStyle("notification-container", org.optimum_tech.quranify.pages.contacts.NotificationContainerStyle)
        ctx.theme.registerStyle("notification", org.optimum_tech.quranify.pages.contacts.NotificationStyle)
        ctx.theme.registerStyle("notification-title", org.optimum_tech.quranify.pages.contacts.NotificationTitleStyle)
        ctx.theme.registerStyle("notification-message", org.optimum_tech.quranify.pages.contacts.NotificationMessageStyle)
        ctx.theme.registerStyle("mirror-background", org.optimum_tech.quranify.pages.policy.mirrorBackground)
        ctx.theme.registerVariant("-circle", org.optimum_tech.quranify.CircleButtonVariant
        )
        ctx.theme.registerVariant("-uncolored", org.optimum_tech.quranify.UncoloredButtonVariant
        )
        ctx.theme.registerKeyframes("side-menu-slide-in", org.optimum_tech.quranify.components.sections.SideMenuSlideInAnim)
        ctx.theme.registerKeyframes("float", org.optimum_tech.quranify.pages.about.floatAnimation)
        ctx.theme.registerKeyframes("pulse", org.optimum_tech.quranify.pages.about.pulseAnimation)
        org.optimum_tech.quranify.initColorMode(ctx)
        org.optimum_tech.quranify.initStyles(ctx)
        org.optimum_tech.quranify.initSiteStyles(ctx)
        org.optimum_tech.quranify.initTheme(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)), UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("_kobweb-root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "_kobweb-root") {
        org.optimum_tech.quranify.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
